@import url('https://fonts.googleapis.com/css2?family=Ms+Madi&display=swap');

.navbar{
    
    position: fixed;
    background-color: #2f4850;
    transition: width 200ms ease-in-out;
}

.navbar-nav{
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.nav-item{
    width: 100%;
}

.nav-item:hover{
    background-color: #426b78;
    transition: background-color 200ms ease-in-out;
}

.nav-link{
    display: flex;
    align-items: center;
    height: 5rem;
    color: aliceblue;
    text-decoration: none;
}

.link-text{
    display: none;
    margin-left: 1rem;
}

.nav-link .icon{
    min-width: 2rem;
    margin: 0 1.5rem;
}

.navbar:hover{
    width: 16rem;
}

.navbar:hover .link-text{
    display: block;
}

.logo{
    font-weight: bold;
    margin-bottom: 1rem;
    text-align: center;
    font-size: 1.5rem;
    letter-spacing: 0.3ch;
    width: 100%;
}

.link-text{
    font-weight: 300;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.logo .link-text{
    font-size: 2.5rem;
    font-family: 'Ms Madi',
    cursive;
}

.logo .icon{
    transform: rotate(0deg);
    transition: transform 600ms;
}

.navbar:hover .logo .icon {
    transform: rotate(-180deg);
}

#home-icon{
    color: yellow;
}

#projects-icon{
    color: rgb(242, 7, 132);
}

#contact-icon{
    color: aqua;
}

@media only screen and (max-width: 600px){
    .navbar {
        bottom: 0;
        width: 100vw;
        height: 5rem;
    }

    .logo{
        display: none;
    }

    .navbar-nav{
        flex-direction: row;
    }

    .nav-link{
        justify-content: center;
    }
}

@media only screen and (min-width: 600px) {
    .navbar{
        top: 0;
        width: 5rem;
        height: 100vh;
    }

    .navbar:hover{
        width: 16rem;
    }

    .navbar:hover .link-text{
        display: inline;
        transform: opacity 600ms;
    }
}